import React, { Component } from 'react';
import BrowserRouter from 'react-router-dom/BrowserRouter';
import Route from 'react-router-dom/Route';
import Switch from 'react-router-dom/Switch';

import NavigationSignedIn from './components/SignedIn/Navigation';
import NavigationSignedOut from './components/SignedOut/Navigation';
 
import Home from './components/SignedOut/Home';
import About from './components/SignedOut/About';
import Contact from './components/SignedOut/Contact';
import Error from './components/404/Error';
import SignIn from './components/SignedOut/Accounts/SignIn';
import SignUp from './components/SignedOut/Accounts/SignUp';
import Pricing from './components/SignedOut/Pricing';

import MyAccount from './components/SignedIn/Accounts/MyAccount';

import Footer from './components/Footer';
 
export default class App extends Component {
  render() {
    return (      
      <div>
        <BrowserRouter>
        <div>
            <Switch>
              {/* Signed in */}
            <Route path="/account" component={NavigationSignedIn}/> 
              {/* Signed out */}
            <Route component={NavigationSignedOut}/>
          </Switch>
        </div> 
      </BrowserRouter>
       <BrowserRouter>
        <div>
            <Switch>
              {/* Signed out */}
             <Route path="/" component={Home} exact/>
             <Route path="/about" component={About}/>
             <Route path="/contact" component={Contact}/>
             <Route path="/login" component={SignIn}/>
             <Route path="/signup" component={SignUp}/>
             <Route path="/pricing" component={Pricing}/>
             {/* Signed in */}
            <Route path="/account" component={MyAccount}/> 
            {/* Error */}
            <Route component={Error}/>
           </Switch>
        </div> 
      </BrowserRouter>
      <Footer />
      </div>
    );
  }
}